<template>
    <div class="loading-box" v-if="show">
      <div class="toast">
        <img src="data:image/gif;base64," />
        <p class="text">{{ text || 'loading' }}</p>
      </div>
    </div>
</template>
<script>
import { Order } from '../Order.js'
export default {
  props: {
    text: String
  },
  created () {
    Order.$on('Loading', (message) => {
      if(message === 'show'){
        this.show = true
      }
      else{
        this.show = false
      }
    })
  },
  data () {
    return {
      show: false
    }
  }
}
</script>

<style scoped>
.loading-box .toast{
    position: fixed;
    z-index: 5000;
    width: 7.6em;
    min-height: 7.6em;
    bottom: 40%;
    left: 50%;
    margin-left: -3.8em;
    background: rgba(17, 17, 17, 0.7);
    text-align: center;
    border-radius: 5px;
    color: #FFFFFF;
}
.loading-box img {
    height: 60px;
    margin-top: 20px;
}
.loading-box .text {
    color: #dddddd;
}
</style>


