<template>
  <div class="search-box">
    <div class="text-bar">
      <input v-model="searchText" class="text-input" type="text" name="fname" placeholder="搜索"/>
      <img v-show="searchText" @click="clear" class="forgo" src="data:image/png;base64," />
    </div>
  </div>
</template>

<script>
import { Order } from '../Order.js'
export default {
  data () {
    return {
      searchText:""
    }
  },
  methods: {
    clear () {
      this.searchText = ''
    }
  },
  watch: {
    searchText(val) {
      Order.$emit('Search', val)
    }
  }
}
</script>

<style lang="less" scoped>
.search-box{
    height: 30px;
    background-color: #f8f8f8;
    border-bottom: 1px solid #dfdde8;
    padding: 5px 10px;
    .text-bar{
        background-color: #dbdbdb;
        height: 30px;
        width: 100%;
        margin: 0 auto;
        border-radius: 5px;
        position: relative;
    }
    .text-input{
        width: 100%;
        height: 100%;
        margin: 0 auto;
        border: none;
        padding: 0;
        background-color: #dbdbdb;
        border-radius: 5px;
        text-align: center;
        font-size: 0.9rem;
    }
    .forgo{
        position: absolute;
        right: 5px;
        top: 5px;
        height: 20px;
    }
}
</style>


